import React, { useEffect, useState } from "react";
import "./App.css";
import { DoctorSvg } from "./components/icons/DoctorSvg";
import { CapsuleSvg } from "./components/icons/CapsuleSvg";
import { DrugStoreSvg } from "./components/icons/DrugStoreSvg";
import { Steps } from "antd";
import Icon from "@ant-design/icons";
import { createStepData } from "./lib/createData";
import { Drawer } from "antd";
import moment from "moment";
const { Step } = Steps;

const responseDateFormat = "DD.MM.YYYY HH:mm:ss";
const requestDateFormat = "DD.MM.YYYY";

const stepIcons = {
  1: (props) => <Icon component={CapsuleSvg} {...props} />,
  2: (props) => <Icon component={DrugStoreSvg} {...props} />,
  3: (props) => <Icon component={DrugStoreSvg} {...props} />,
};

const createTextStepField = (isClose, text) => {
  return isClose ? (
    <s>{text}</s>
  ) : (
    <span style={{ color: "black" }}>{text}</span>
  );
};
const createTitleStepField = (isClose, text, item) => {
  return isClose ? (
    <s className='stepName'>{`${text} ${item.item.name}`}</s>
  ) : (
    <span className='stepName'>
      <b  style={{ color: "black" }}>{`${text}`} </b>
      <span style={{ color: "black" }} >{item.item.name}</span>
    </span>
  );
};

const createIconStep = (type) => {
  return stepIcons[type]();
};

const request = (param) => {
  return fetch(`${window.location.origin}/index.php?${param}`);
};
