function App() {
  const [isDrawerOpen, setDrawerOpen] = useState(false);
  const [openData, setOpenData] = useState(null);
  const [stepData, setStepData] = useState([]);
  const [showDate, setShowDate] = useState(new Date());

  const loadData = async () => {
    const result = await request(
      `r=user-timetable/list&date=${moment(showDate).format(
        requestDateFormat
      )}`
    );
    const resultJSON = await result.json();
    setStepData(resultJSON);
    setDrawerOpen(false);
  };

  const onStepSuccess = async () => {
    const result = await request(`r=user-timetable/complete&id=${openData.id}`);
    loadData();
  };

  const onSkipStep = async () => {
    const result = await request(`r=user-timetable/skip&id=${openData.id}`);
    loadData();
  };

  useEffect(() => {
    loadData(showDate);
  }, [showDate]);

  const onDrawerOpen = () => setDrawerOpen(true);
  const onDrawerClose = () => setDrawerOpen(false);
  const onClickStep = (step) => {
    if(step.type === 2){
      window.location.href=`index.php?r=user-store/create&substance_id=${step.item.substance_id}`
      return;
    }
    setOpenData(step);
    onDrawerOpen();
  };

  const onClickNextDate = () => {
    setShowDate(moment(showDate).add(1, "day").toDate());
  };
  const onClickPrevDate = () => {
    setShowDate(moment(showDate).subtract(1, "day").toDate());
  };

  const createDrawerData = (step) => {
    if (!step) {
      return <></>;
    }
    if (step.type === 1) {
      return (
        <div className={"buttonWrapperStep"}>
          <button
            onClick={onStepSuccess}
            type="button"
            className="btn btn-sm btn-hmetac btn-success"
          >
            Принять
          </button>
          <button
            onClick={onSkipStep}
            type="button"
            className="btn btn-sm btn-hmetac btn-warning"
          >
            Пропустить
          </button>
        </div>
      );
    } else {
      return <div></div>;
    }
  };

  const createDate = (date) => {
    if (
      moment(date).format(requestDateFormat) ===
      moment(new Date()).format(requestDateFormat)
    ) {
      return "Сегодня";
    }
    return moment(date).format(requestDateFormat);
  };

  return (
    <div className="App">
      <div className="dateButtonTime">
        <button className='btn btn-sm btn-hmetac btn-primary' onClick={onClickPrevDate}>
          {moment(showDate).subtract(1, "day").format(requestDateFormat)}
        </button>
        <button className='btn btn-sm btn-hmetac btn-primary'  onClick={onClickNextDate}>
          {moment(showDate).add(1, "day").format(requestDateFormat)}
        </button>
      </div>
      <h2>{createDate(showDate)}</h2>
      <Drawer
        title={<p className={"titleTile"}>{openData?.item?.name ?? ""}</p>}
        placement="right"
        closable={false}
        onClose={onDrawerClose}
        visible={isDrawerOpen}
      >
        <>{createDrawerData(openData)}</>
      </Drawer>
      <Steps direction="vertical" size={"default"}>
        {createStepData(stepData).map((item, i) => {
          return (
            <Step
              key={i}
              onClick={() => {
                if (item.complete) return;
                onClickStep(item);
              }}
              status={item.status}
              title={createTitleStepField(
                item.complete,
                moment(item.date, responseDateFormat).format("HH:mm"),
                item
              )}
              description={createTextStepField(item.complete, item.content)}
              icon={createIconStep(item.type)}
              className={`${item.status} ${item.complete && 'complete'}`}
              disabled={item.complete ? true : false}
            />
          );
        })}
      </Steps>
    </div>
  );
}

export default App;
